
import vNav from "../../../components/nav";
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
name: 'home',
components: {
Swiper,
vNav
},
data () {
return {
banner: null,
device: null,
mycode: null
}
},
created(){
this.loading.show();
let token = this.cookie.get('token');
let id = this.cookie.get('id');
let code = this.$route.query.code;

if (id) this.mycode = id;
if (token) this.get_banner(token);
else if (code) {
this.ajax('login', `code=${code}`, this.login);
} else this.loading.hide();

},
methods: {
get_banner(token){
this.ajax('get_banner', `token=${token}&limit_begin=0&limit_num=20`, this.init_banner);
},
init_banner(d){
if (d.code !== 200) this.toast(d.descrp);
else {
let banner = [];
banner = d.banner.pic.map((item) => {
return {
url: "javascript;",
img: item.img,
id: item.id,
fallbackImg: "./static/img/banner.png"
}
});
this.banner = banner;
this.device = d.info.device;
}
this.loading.hide()
},
login(d){
this.loading.hide();
if (d.code !== 200) {
this.toast(d.descrp);
this.$router.push('login')
}
else {
let token = d.data.api_token;
this.cookie.set("token", token);
this.cookie.set("id", d.data.id);
this.cookie.set("balance", d.data.balance);
this.cookie.set('username', d.data.user_nicename);
this.cookie.set('avatar', d.data.avatar);
this.mycode = d.data.id;
this.get_banner(token)
}
},
invite(){


let mycode = this.mycode + "68320";
this.$wechat.onMenuShareAppMessage({
title: '快抓娃娃机，抓中带回家', // 分享标题
desc: '跟我一起玩跨抓娃娃吧，输入邀请码' + mycode, // 分享描述
link: window.location.origin, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
imgUrl: './static/img/logo', // 分享图标

});
this.alert('请点击右上角')
},
_open(page){
this.$router.push(page);
}
}
}



